import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {useDispatch} from 'react-redux';
import {logOutSuccess} from '../../store/modules/auth/auth.action';
import MenuItems from './menu-items';
import {getModules} from '../../api/account.api';
import { useEffect, useState } from 'react';

const AppNavbar = () =>{
    const dispatch = useDispatch();
    const [modules, setModules] = useState(null)
    useEffect(() => {
        getModules().then(resp => { 
            setModules(resp.data.response);
        })
    }, [])

    return ( 
       
            <Navbar expand="lg">
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        
                        {modules?.map((item, index) =>
                            <Nav.Link as={Link} key={index} to="/dashboard">{item.name}</Nav.Link>
                        )}
                        
                        <NavDropdown title="Masters" id="masters">
                            <NavDropdown.Item as={Link} to="/category">Category</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/departments">Department</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/designation">Designation</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/degree">Degree</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/specialization">Specialization</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/role">Role</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/branch">Branch</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/student-status">Student Status</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/country">Country</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/state">State</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/admissiontype">Admission Types</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/menus">Menus/Sub Menus</NavDropdown.Item>
                        </NavDropdown>
                        <NavDropdown title="Others" id="others">
                            <NavDropdown.Item as={Link} to="/students-list">Student Registration</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/access-rights">Access Rights</NavDropdown.Item>
                        </NavDropdown>
                        <MenuItems/>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
    )
}
export default AppNavbar;