import {  useSelector } from "react-redux";
import { Fragment } from "react";
import { Navbar, Nav, NavDropdown, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {useDispatch} from 'react-redux';
import {logOutSuccess} from '../../store/modules/auth/auth.action';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCog } from '@fortawesome/free-solid-svg-icons';
import user from '../../assets/images/user.png';

const UserInfo = () => {
    const dispatch = useDispatch(); 
    const userInfo = useSelector(state => state.auth.userInfo);
    return (
        <Fragment >
            {
            userInfo ?
                <Navbar expand="lg">
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="mr-auto">
                            <NavDropdown title={<img src={user} alt="user" className="user-img img img-responsive" />} id="Settings">
                                <small style={{display: "block", color: "#fb9d35", fontWeight: "bold"}}>{userInfo ? "Welcome, "+ userInfo.username : ""}</small>
                                <hr/>
                                <NavDropdown.Item as={Link} to="/">My Activites</NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/">Change Password</NavDropdown.Item>
                                <hr />
                                <Button variant="danger" className="form-control" size="sm" onClick={()=>dispatch(logOutSuccess())} >Logout</Button>
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>:""
            }
        </Fragment>
    )
}

export default UserInfo;