
//Lib import 
import { useHistory } from 'react-router-dom'
import { Container, Row, Col } from 'react-bootstrap';
import { Form, Formik } from 'formik';
import * as Yup from 'yup';
import { useDispatch } from 'react-redux';

//imports
import AppLogo from "../layout/AppLogo";
import { LoginCaptcha, LoginInput, LoginSelect } from '../components/SignIn/LoginControls';
import ResetPassword from '../components/SignIn/ResetPassword';
import { sessionYears } from '../utils/utilityFunctions';
import { doLogin } from '../api/account.api';
import { signInSuccess } from '../store/modules/auth/auth.action';
import { Language } from '@material-ui/icons';

const lang = require('../language/en.json');


const SignIn = () => {

  // const history = useHistory();
  const dispatch = useDispatch();

  const sessionYearList = sessionYears();

  const initialValues = {
    username: "",
    password: "",
    fyear: sessionYearList[0],
    captcha: undefined
  }

  const validationSchema = Yup.object().shape({
    username: Yup.string().required("Please enter username"),
    password: Yup.string().required("Please enter password"),
    captcha: Yup.boolean().required("Please enter the captcha").test('xyz', "Invalid Captcha", (value) => {
      return value
    })
  })

  const onSubmit = (values, formikProps) => {

    const { username, password, fyear } = values;
    doLogin({ username: username, password, fyear }).then(resp => {
      console.log(resp);
      const { authToken: token, user: userInfo } = resp.data.response;
        dispatch(signInSuccess({ token, userInfo }));
        formikProps.setSubmitting(false); 
        // history.push("/");
    }).catch(e => {      
      formikProps.setSubmitting(false);     
    })

  }

  return (
    <Container fluid>
      <Row className="justify-content-md-center">
        <Col md={4}>
          <div className="loginform">
            <Row>
              <Col md={3}> <AppLogo /> </Col>
              <Col> <b style={{ lineHeight: 2 }}>{process.env.REACT_APP_NAME}</b></Col>
            </Row>
            <hr />
            <h6 className="form-title">{lang.welcometo}{process.env.REACT_APP_TITLE}</h6>

            <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
              {(formik) => {
                return (
                  <>
                    <Form className="login-form">
                      <LoginInput name="username" type="text" placeholder="Username" />
                      <LoginInput name="password" type="password" placeholder="Password" />
                      <LoginSelect name="fyear">
                        {sessionYearList.map((ny) => (<option value={ny} key={ny}>{ny}</option>))}
                      </LoginSelect>
                      <LoginCaptcha name="captcha" />
                      <div className="form-group1 form-button">
                        <input type="submit" name="signin" id="signin" className="form-submit" value="Log in" disabled={!(formik.dirty && formik.isValid) || formik.isSubmitting} />
                      </div> 
                    </Form>
                    <ResetPassword />
                    <br />
                    <b className="alertmessage">{lang.pleaseDoNotShareYourPasswordYouWillBeResponsibleForAllActivities}</b>
                  </>
                )
              }}
            </Formik>
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default SignIn;