import HTTP from '../service/http';
import { methods } from '../constants';
import {
    __endpoint_getCategories,
    __endpoint_doLogin,
    __endpoint_getDepartments,
    __endpoint_getDesignations,
    __endpoint_getModules,
    __endpoint_getForm,
    __endpoint_getForms,
    __endpoint_getRoles,
    __endpoint_getBranches,
    __endpoint_getStudentStatus,
    __endpoint_getPrograms,
    __endpoint_getDegrees,
    __endpoint_getSpecializations,
    __endpoint_getCountries,
    __endpoint_getStates,
    __endpoint_getAdmissionType
} from '../constants/endpoints';

export const doLogin = payload => HTTP.Request(methods.POST, __endpoint_doLogin, payload);
export const getCategories = payload => HTTP.Request(methods.GET, __endpoint_getCategories, payload);
export const getDepartments = payload => HTTP.Request(methods.GET, __endpoint_getDepartments, payload);
export const getDesignations = payload => HTTP.Request(methods.GET, __endpoint_getDesignations, payload);
export const getModules = payload => HTTP.Request(methods.GET, __endpoint_getModules, payload);
export const getRoles = payload => HTTP.Request(methods.GET, __endpoint_getRoles, payload);
export const getBranches = payload => HTTP.Request(methods.GET, __endpoint_getBranches, payload);
export const getStudentStatus = payload => HTTP.Request(methods.GET, __endpoint_getStudentStatus, payload);
export const getPrograms = payload => HTTP.Request(methods.GET, __endpoint_getPrograms, payload);
export const getDegrees = payload => HTTP.Request(methods.GET, __endpoint_getDegrees, payload);
export const getSpecializations = payload => HTTP.Request(methods.GET, __endpoint_getSpecializations, payload);
export const getCountries = payload => HTTP.Request(methods.GET, __endpoint_getCountries, payload);
export const getStates = payload => HTTP.Request(methods.GET, __endpoint_getStates, payload);
export const getAdmissionTypes = payload => HTTP.Request(methods.GET, __endpoint_getAdmissionType, payload);
export const getForm = payload => HTTP.Request(methods.GET, __endpoint_getForm + payload.id);
export const getForms = payload => HTTP.Request(methods.GET, __endpoint_getForms + payload.id);

