import {LOG_OUT_SUCCESS, SIGN_IN_SUCCESS, UPDATE_USER_INFO } from './auth.types';
import Session from '../../../service/session';

const initialState = {
    token: Session.get('token') || undefined,
    userInfo : Session.getObject('user') || undefined
}

const authReducer = (state=initialState, payload)=> {
    const {type, data} = payload;
    switch(type){
        case SIGN_IN_SUCCESS:
            return {...state, ...data};   
        case LOG_OUT_SUCCESS:
            return {...state, token: "", userInfo: null }
        case UPDATE_USER_INFO:
            return {...state, userInfo: data } 
        default:
            return state;
    }
}

export default authReducer;