import { SIGN_IN_SUCCESS, LOG_OUT_SUCCESS, UPDATE_USER_INFO } from './auth.types';

import Session from '../../../service/session';

export const signInSuccess = (data) => dispatch => {
    Session.set("token", data.token);
    Session.setObject("user", data.userInfo);
    dispatch({
        data,
        type: SIGN_IN_SUCCESS,
    });
};

export const logOutSuccess = () => dispatch => {
    Session.remove("token");
    Session.remove("user");
    dispatch({
        type: LOG_OUT_SUCCESS
    });
};


export const updateUserInfo = userInfo => ({type: UPDATE_USER_INFO, data: userInfo})
