import { useField } from 'formik';
import Captcha from "react-numeric-captcha";

/*Custom controls for specific to login page*/


//Helper control to show error message 
const LoginHelperText = ({ message }) => {
    return (
        <div className="text-danger" style={{ textAlign: "left", fontStyle: "italic" }}>{message}</div>
    )
}

//Custom input control to use with formik
export const LoginInput = ({ ...props }) => {
    const [field, meta] = useField(props);

    return (
        <>
            <div className="form-group">
                <input {...field} {...props} className="form-control has-error" />
                {meta.touched && meta.error ? (<LoginHelperText message={meta.error} />) : null}
            </div>
        </>
    )
}

//Custom select control to use with formik
export const LoginSelect = ({sessionYears, ...props }) => {
    const [field, meta] = useField(props);
    return (
        <>
            <div className="form-group">
                <select {...field} {...props} className="form-control" />
                {meta.touched && meta.error ? (<LoginHelperText message={meta.error} />) : null}
            </div>
        </>
    )
}

//Wrapper on captch control to work with formik
export const LoginCaptcha = ({ ...props }) => {
    const [field, meta, helpers] = useField(props);
    const { setValue } = helpers;
    return (
        <>
            <div className="form-group" {...field}>
                <Captcha onChange={setValue} />
                {meta.touched && meta.error ? (<LoginHelperText message={meta.error} />) : null}
            </div>
        </>
    )
}