import React from 'react';
import { Route, Redirect } from 'react-router-dom';
const PrivateRoute = ({ authToken, children, ...rest }) => {

    return (
      <Route
        {...rest}
        render={() =>
          authToken ? (
            children
          ) : (
              <Redirect to="/signin" />
            )
        }
      />
    ); 
}

export default PrivateRoute