import React, {useState} from 'react';
import { Switch, Route, Redirect, useLocation } from 'react-router-dom';
import MainApp from './layout/Main';
import SignIn from './pages/SignIn';

import { useSelector } from 'react-redux';
import { ToastContainer } from 'react-toastify';
import { Col, Container, Row } from 'react-bootstrap';
import LeftSidebar from './components/common/LeftSidebar';
import PrivateRoute from './router/PrivateRoute';

const themes = {
  red: '#ce4747',
  yellow: '#d89a28',
  green: '#65bc7b',
  blue: '#468ed4'
}

const App = () => {
  const [activeTheme, setactiveTheme] = useState(localStorage.getItem('activeTheme')?localStorage.getItem('activeTheme'):'red')
  let [activeFontSize, setactiveFontSize] = useState(localStorage.getItem('activeFontSize')?Number(localStorage.getItem('activeFontSize')):14)
  const authToken = useSelector(state => state.auth.token);
  // const location = useLocation();

  //If user is already logged in redirect to da
  if(authToken && window.location.pathname==="/signin"){
    return <Redirect to={"/"} />;
  }
  const themeUpdate = (activeTheme, activeFontSize) => {
    setactiveTheme(activeTheme)
    localStorage.setItem('activeTheme', activeTheme)
    if (activeFontSize > 11 && activeFontSize < 17 ) {
      localStorage.setItem('activeFontSize', activeFontSize)
      setactiveFontSize(activeFontSize)
    }
  }
  return (
    <div className={'app theme-'+activeTheme+' font-'+activeFontSize}>
      <div className="theme-setter">
        <ul className="color-options">
          {
            Object.entries(themes).map((theme, index) => {
              return <li className={activeTheme===theme[0]?'theme-font-size active':'theme-font-size'} key={index} style={{backgroundColor: theme[1]}} onClick={() => themeUpdate(theme[0], activeFontSize)}></li>
            })
          }
        </ul>
        <ul className="font-options">
          <li className="decrease" disabled={activeFontSize===12} onClick={()=>themeUpdate(activeTheme, activeFontSize-1)}>A</li>
          <li className="increase" disabled={activeFontSize===16} onClick={()=>themeUpdate(activeTheme, activeFontSize+1)}>A</li>
        </ul>
      </div>
      <Switch>
        <Route path="/signin">
          <SignIn />
        </Route>
        <PrivateRoute authToken={authToken}>
          <LeftSidebar />
          <div className="maincontent">
            <Container fluid>
              <Row>
                <Col md={12} lg={12} sm={12}>
                  <MainApp />
                </Col>
              </Row>
            </Container>
          </div>
        </PrivateRoute>
      </Switch>
      <ToastContainer></ToastContainer>
    </div>
  );
}

export default App;
