export const sessionYears = () => {
    const cy = new Date().getFullYear();
    const yearsList = [];
    for (let y = cy; y >= process.env.REACT_APP_FIRST_SESSION; y--) {
        yearsList.push(y + '-' + (y % 100 + 1))
    }
    return yearsList;
}
export const e_print = (e) => {
    return (e.errors===undefined) ? (e.error===undefined?e:e.error): e.errors[0]
}
