import {SHOW_LOADING, HIDE_LOADING, SET_FORM_DATA, SET_MODULE_DATA, TOGGLE_SUB_MODULE, TOGGLE_SUB_MODULE_FORM, ACTIVE_SUB_MODULE } from './app.types';

const initialState = {
    loading: false,
    formData: undefined,
    moduleData: undefined
}

const appReducer = (state=initialState, payload)=> {
    switch(payload.type){
        case SHOW_LOADING:
            return {...state, loading: true};
        case HIDE_LOADING:
            return {...state, loading: false};
        case SET_FORM_DATA:
            return {...state, formData: payload.formData};
        case SET_MODULE_DATA:
            return {...state, moduleData: payload.moduleData};
        case TOGGLE_SUB_MODULE:
            return {...state, toggleSubModule: payload.value};
        case TOGGLE_SUB_MODULE_FORM:
            return {...state, toggleSubModuleForm: payload.value};
        case ACTIVE_SUB_MODULE:
                return {...state, activeSubModule: payload.value};
        default:
            return state;
    }
}

export default appReducer;