import { Button } from 'react-bootstrap';
import { useState } from 'react';
import { Modal } from 'react-bootstrap';


const ResetPassword = () => {

    const [show, setShow] = useState(false);

    return (
        <>
        <Button className="theme-color" variant="link"  onClick={() => setShow(true)} className="forgotText">
                Forgot / Reset Password
        </Button>
            <Modal
                size="md"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                show={show}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        <p className="theme-font-size">Please enter your username to reset your password.</p>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <form method="POST" className="register-form" id="login-form">
                        <div className="form-group">
                            <label className="theme-font-size" htmlFor="fusername"><i className="zmdi zmdi-account material-icons-name"></i></label>
                            <input type="text" name="fusername" id="fusername" className="form-control" placeholder="Username" />
                        </div>
                        <div className="form-group form-button">
                            <input type="submit" name="reset" id="reset" className="form-submit btn btn-success" value="Submit" />
                        </div>
                    </form>
                </Modal.Body>
                <Modal.Footer>
                <Button className="theme-color" variant="link"  onClick={() => setShow(false)} className="forgotText">
                    Close
                </Button>
                </Modal.Footer>
            </Modal>

        </>
    )
}

export default ResetPassword;