import React, { lazy } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import Error404 from '../pages/Error404';
const AppRoute = () => {
    return (
        <Switch>
            <Route path="/dashboard" component={lazy(() => import('../pages/Dashboard'))} />
            <Route path="/settings" component={lazy(() => import('../pages/Setting'))} />
            <Route path="/category" component={lazy(() => import('../pages/masters/Category'))} />
            <Route path="/departments" component={lazy(() => import('../pages/masters/Department'))} />
            <Route path="/designation" component={lazy(() => import('../pages/masters/Designation'))} />
            <Route path="/role" component={lazy(() => import('../pages/masters/Role'))} />
            <Route path="/branch" component={lazy(() => import('../pages/masters/Branch'))} />
            <Route path="/student-status" component={lazy(() => import('../pages/masters/StudentStatus'))} />
            <Route path="/program" component={lazy(() => import('../pages/masters/Program'))} />
            <Route path="/degree" component={lazy(() => import('../pages/masters/Degree'))} />
            <Route path="/specialization" component={lazy(() => import('../pages/masters/Specialization'))} />
            <Route path="/academicDepartment" component={lazy(() => import('../pages/AcademicDepartment'))} />
            <Route path="/students-list" component={lazy(() => import('../pages/student_registration/StudentsList'))} />
            <Route path="/student" component={lazy(() => import('../pages/student_registration/Student'))} />
            <Route path="/access-rights" component={lazy(() => import('../pages/AccessRights'))} />
            <Route path="/state" component={lazy(() => import('../pages/masters/State'))} />
            <Route path="/country" component={lazy(() => import('../pages/masters/Country'))} />
            <Route path="/admissiontype" component={lazy(() => import('../pages/masters/AdmissionType'))} />
            <Route path="/menus" component={lazy(() => import('../pages/masters/Menus'))} />
          
            <Route path="/test" component={lazy(() => import('../pages/Test'))} />
            <Route exact path="/">
                <Redirect to="/dashboard" />
            </Route>
            <Route path="/error">
                <Error404 />
            </Route>
            <Route path="*">
                <Redirect to="/error" />
            </Route>
        </Switch>
    )
}

export default AppRoute;
